"use client"

import { useState } from "react"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { templates, getTemplate } from "@/config/templates"

export default function CreatePage() {
  const [selectedTemplate, setSelectedTemplate] = useState<string>("alerta_nacional")
  const [generatedLink, setGeneratedLink] = useState<string | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const handleCreateLink = async () => {
    setLoading(true)
    setError(null)
    try {
      const response = await fetch('/api/tracking-links', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          templateType: selectedTemplate
        })
      })

      if (!response.ok) {
        throw new Error('Error al crear el enlace')
      }

      const data = await response.json()
      const fullUrl = `${window.location.origin}/track/${data.shortCode}`
      setGeneratedLink(fullUrl)
    } catch (err: any) {
      setError(err.message || 'Error al generar el enlace')
    } finally {
      setLoading(false)
    }
  }

  const handleCopyLink = () => {
    if (generatedLink) {
      navigator.clipboard.writeText(generatedLink)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 to-black text-white">
      <div className="container mx-auto p-6">
        <div className="max-w-4xl mx-auto space-y-8">
          <Card className="bg-black/40 backdrop-blur-sm border-gray-800">
            <CardContent className="p-6">
              <h1 className="text-3xl font-bold mb-6 text-center">
                Generador de Enlaces
              </h1>

              <div className="space-y-8">
                {/* Selector de Plantillas */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                  {Object.entries(templates).map(([key, template]) => (
                    <div
                      key={key}
                      className={`
                        p-4 rounded-lg border cursor-pointer transition-all
                        ${selectedTemplate === key
                          ? 'border-blue-500 bg-blue-500/10'
                          : 'border-gray-700 hover:border-gray-600'
                        }
                      `}
                      onClick={() => setSelectedTemplate(key)}
                    >
                      <div className="text-2xl mb-2">{template.icon}</div>
                      <h3 className="font-medium mb-1">{template.title}</h3>
                      <p className="text-sm text-gray-400">{template.description}</p>
                    </div>
                  ))}
                </div>

                {/* Vista Previa */}
                {selectedTemplate && (
                  <div className="border border-gray-700 rounded-lg p-4">
                    <h3 className="text-lg font-medium mb-4">Vista Previa</h3>
                    <div className="space-y-2">
                      <p><strong>T√≠tulo:</strong> {getTemplate(selectedTemplate).title}</p>
                      <p><strong>Mensaje:</strong> {getTemplate(selectedTemplate).message}</p>
                      <p><strong>Validez:</strong> {getTemplate(selectedTemplate).validityPeriod}</p>
                    </div>
                  </div>
                )}

                {/* Bot√≥n de Generaci√≥n */}
                <div className="flex flex-col items-center gap-4">
                  <Button
                    onClick={handleCreateLink}
                    disabled={loading}
                    className="w-full max-w-md bg-gradient-to-r from-blue-600 to-blue-800"
                  >
                    {loading ? (
                      <div className="flex items-center gap-2">
                        <div className="animate-spin rounded-full h-4 w-4 border-2 border-current" />
                        <span>Generando enlace...</span>
                      </div>
                    ) : (
                      <>
                        <span className="mr-2">üîó</span>
                        Generar Enlace de Rastreo
                      </>
                    )}
                  </Button>

                  {error && (
                    <div className="text-red-400 text-sm">{error}</div>
                  )}

                  {generatedLink && (
                    <div className="w-full max-w-md bg-blue-500/10 border border-blue-500/50 rounded-lg p-4">
                      <p className="font-medium mb-2">Enlace generado:</p>
                      <div className="flex items-center gap-2">
                        <input
                          type="text"
                          value={generatedLink}
                          readOnly
                          className="flex-1 bg-black/50 border border-gray-700 rounded px-3 py-2 text-sm"
                        />
                        <Button
                          onClick={handleCopyLink}
                          className="bg-blue-600 hover:bg-blue-700"
                          size="sm"
                        >
                          üìã Copiar
                        </Button>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
